  text-transform: none;
  display: block;
  font-size: 16px;
  font-weight: 300;
  font-style: italic;
  font-family: "Crimson Pro", serif;
}
.flc-comparison tbody tr:last-child td .flc-btn {
  padding: 11px 15px;
  font-size: 12px;
  line-height: 15px;
  margin: 5px auto 0;
}
.flc-comparison .table-check {
  display: block;
  width: 30px;
  height: 22px;
  margin: 0 auto;
}
@media (max-width: 991px) {
  .flc-comparison .table-check {
    width: 26px;
    height: 18px;
  }
}
.flc-comparison .table-check img {
  width: 100%;
}

.flc-text-block {
  background-color: #f8f7f4;
  padding: 75px 0;
}
@media (max-width: 991px) {
  .flc-text-block {
    padding: 60px 0;
  }
}
@media (max-width: 767px) {
  .flc-text-block {
    padding: 50px 0;
  }
}
.flc-text-block__title {
  color: #003d47;
  font-family: "EB Garamond", serif;
  margin: 0 0 10px;
}
.flc-text-block__subtitle {
  color: #003d47;
  font-family: "Crimson Pro", serif;
  font-weight: 300;
  font-style: italic;
  margin: 0 0 25px;
}
.flc-text-block__text p {
  margin: 0 0 25px;
