.flc-accordion {
  padding: 80px 0 90px;
  background-color: #f8f7f4;
}
.flc-accordion__title {
  text-align: center;
  margin: 0 0 45px;
  font-family: "EB Garamond", serif;
  color: #003d47;
}
.flc-accordion__item {
  margin-bottom: 1px;
}
.flc-accordion__item:last-child {
  margin-bottom: 0;
}
.flc-accordion__item.active .flc-accordion__item-header {
  background-color: #003d47;
  color: #E8E4DA;
}
.flc-accordion__item.active .flc-accordion__item-header span {
  transform: rotate(-270deg);
}
.flc-accordion__item.active .flc-accordion__item-header span svg path {
  fill: #E8E4DA !important;
}
.flc-accordion__item.active .flc-accordion__item-body {
  display: block;
}
.flc-accordion__item-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Crimson Pro", serif;
  font-weight: 300;
  font-style: italic;
  color: #003d47;
  line-height: 1;
  cursor: pointer;
  padding: 18px 20px;
  background-color: #E8E4DA;
  transition: all 0.25s;
}
.flc-accordion__item-header span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  transform: rotate(-90deg);
  margin-left: 10px;
  transition: all 0.25s;
}
.flc-accordion__item-header span svg {
  height: 20px;
  width: 9px;
}
.flc-accordion__item-header span svg path {
  transition: all 0.25s;
  fill: #003d47 !important;
}
.flc-accordion__item-body {
  border: 1px solid #003d47;
  padding: 20px;
  display: none;
}
.flc-accordion__item-body p {
  color: #4A4244;
  margin-bottom: 20px;
}
.flc-accordion__item-body p:last-child {
  margin-bottom: 0;
}